body {
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 400;
  line-height: 150%;
}

h1 {
  font-family: Montserrat;
  font-size: 46px;
  font-weight: 700;
  line-height: 100%;
  letter-spacing: 0.1rem;
}

h2 {
  font-family: Montserrat;
  font-size: 24px;
  font-weight: 700;
  line-height: 100%;
  color: #fff;
}

h3 {
  font-family: Montserrat;
  font-size: 20px;
  font-weight: 700;
  line-height: 120%;
  color: #fff;
}

h6 {
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 600;
  line-height: 150%;
}

.btn-text {
  font-family: Montserrat;
  font-size: 18px;
  font-weight: 500;
  line-height: 150%;
  color: #131313;
}

.caption {
  font-family: Montserrat;
  font-size: 14px;
  font-weight: 500;
  line-height: 100%;
  color: #fff;
}
.caption.medium {
  font-size: 16px;
}
.caption.semibold {
  font-size: 16px;
  font-weight: 600;
  line-height: 140%;
}

.caption-medium {
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 500;
  line-height: 100%;
  color: #fff;
}

.caption-regular {
  font-family: Montserrat;
  font-style: 400;
  font-size: 14px;
  line-height: 100%;
  color: #fff;
}

@media screen and (max-width: 1199px) {
  h1 {
    font-size: 17px;
  }
  h2 {
    font-size: 14px;
  }
  h3 {
    font-size: 14px;
  }
  h4 {
    font-size: 12px;
  }
  h5 {
    font-size: 10px;
    line-height: 120%;
  }
  .subtitle {
    font-size: 15px;
    line-height: 150%;
  }
  body {
    font-size: 12px;
    line-height: 170%;
  }
  .caption {
    font-size: 11px;
    line-height: 120%;
  }
  .btn-text {
    font-size: 11px;
  }
}
.btn-request {
