.icon-phone {
  width: 22px;
  height: 22px;
  mask-image: url(/src/assets/static/img/icon-phone.svg);
  mask-position: center center;
  mask-repeat: no-repeat;
  mask-size: cover;
  background: #a9d7c2;
  margin-right: 5px;
}

.icon-position {
  width: 22px;
  height: 22px;
  mask-image: url(/src/assets/static/img/position.svg);
  mask-position: center center;
  mask-repeat: no-repeat;
  mask-size: cover;
  background: #a9d7c2;
  margin-right: 5px;
}

.viber,
.telegram,
.whats-up {
  width: 34px;
  height: 34px;
  object-fit: cover;
}

.container {
  width: 100%;
  max-width: 1540px;
  margin: auto;
  position: relative;
}
.container-wide {
  width: 100%;
  max-width: 960px;
  margin: auto;
}
.container-medium {
  width: 100%;
  max-width: 760px;
  margin: auto;
}
.container-small {
  width: 100%;
  max-width: 680px;
  margin: auto;
}
@media screen and (max-width: 1199px) {
  .container {
    padding: 0 16px;
  }
}
@media screen and (max-width: 899px) {
  .container {
    padding: 0 16px;
  }
}

.header {
  margin-top: 40px;
  height: 85px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav.caption {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-transform: uppercase;
}

.nav a {
  margin-right: 25px;
  position: relative;
  color: #fff;
}
.nav a:last-child {
  margin-right: 0;
}
.nav a::before {
  content: "";
  position: absolute;
  width: 0;
  height: 2px;
  top: 20px;
  left: 0;
  background: #fff;
  transition: all 0.3s ease;
}
.nav a:hover {
  cursor: pointer;
  color: #a9d7c2;
  transition: all color 0.3s ease;
}

.nav a:hover:before {
  cursor: pointer;
  width: 100%;
  height: 2px;
}

.logo a {
  cursor: pointer;
}

.time {
  margin-bottom: 5px;
}

.number {
  margin-right: 5px;
}

.phone {
  display: flex;
  align-items: center;
}

.phone a:hover {
  cursor: pointer;
  color: #a9d7c2;
}

.position {
  display: flex;
  align-items: center;
}

.header-item.messenger {
  margin-left: 30px;
  display: flex;
  align-items: center;
}
.header-item.messenger a img:hover {
  cursor: pointer;
  transform: scale(1.05);
}

.messenger-item {
  margin-right: 10px;
}

@media screen and (max-width: 1199px) {
  .nav.caption {
    display: none;
  }
  .header-item.duty.caption {
    display: none;
  }
  .header-item.contacts.caption {
    display: none;
  }
}
section.grid {
  margin-top: 60px;
  border-radius: 10px;
  margin-bottom: 60px;
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  border: 1px solid rgba(255, 255, 255, 0.5);
}

.item-wrapper {
  padding: 25px 100px 25px 35px;
}
.item-wrapper.color-ground {
  background-color: #242424;
}
.item-wrapper p {
  color: #fff;
}

.grid-item h1 {
  color: #a9d7c2;
  text-transform: uppercase;
  margin-bottom: 30px;
  white-space: nowrap;
}
.grid-item h1 span {
  color: #131313;
  background: #a9d7c2;
}

.gift {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.gift-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.gift-img {
  margin-right: 20px;
}

.gift-text h2 {
  font-weight: 500;
}
.gift-text h2 span {
  color: #a9d7c2;
  text-transform: uppercase;
  font-weight: 700;
  line-height: 130%;
}

.btn-request.main {
  color: #fff;
  background-color: #131313;
  font-family: Montserrat;
  font-style: 500;
  font-size: 18px;
  border: 2px solid #a9d7c2;
  line-height: 150%;
  border-radius: 15px;
  box-shadow: 0 0 4px rgba(255, 255, 255, 0.2);
}
.btn-request.main:hover {
  cursor: pointer;
  background-color: #242424;
  color: #a9d7c2;
  border: 2px solid #fff;
}

.vidbox {
  position: relative;
  padding-bottom: 56.25%; /* задаёт высоту контейнера для 16:9 (если 4:3 — поставьте 75%) */
  height: 0;
  overflow: hidden;
  border-radius: 0 10px 10px 0;
}

#video {
  width: 100%;
  object-fit: cover;
}

#playbutton {
  position: absolute;
  cursor: pointer;
  mask-image: url(/src/assets/static/img/player.svg);
  mask-position: center center;
  background-color: #fff;
  mask-repeat: no-repeat;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

#playbutton.playing {
  visibility: hidden;
}

@media screen and (max-width: 1199px) {
  .grid {
    grid-template-columns: 1fr;
  }
  .item-wrapper {
    padding: 12px 12px 12px 12px;
  }
  .gift {
    flex-direction: column;
  }
}
.video-shooting-is {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 25px;
