    }

    @for $i from 1 through 16 {
      &t-sm-#{$i} {
        margin-top: 8px * $i;
      }
    }

    @for $i from 1 through 16 {
      &r-sm-#{$i} {
        margin-right: 8px * $i;
      }
    }

    @for $i from 1 through 16 {
